// @flow

import React from 'react'
import styled from 'styled-components'
import {Link} from 'gatsby'

import {rhythm} from '../Utils/Typography'

const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'

type Props = {
  post: {
    frontmatter: {
      title: string,
      date: string,
      description: ?string,
    },
    fields: {
      slug: string,
    },
    excerpt: string,
  },
}

const MyArticle = styled.article`
  font-family: ${FONT_NANUM_GOTHIC};
`

const H3 = styled.h3`
  font-family: ${FONT_NANUM_GOTHIC};
  margin-bottom: ${rhythm(1 / 4)};
`

const MyLink = styled(Link)`
  box-shadow: none;
`

const PostReview = (props: Props) => {
  const {
    post: {
      frontmatter: {title, date, description},
      fields: {slug},
      excerpt,
    },
  } = props

  return (
    <MyArticle key={slug}>
      <header>
        <H3>
          <MyLink to={slug}>{title}</MyLink>
        </H3>
        <small>{date}</small>
      </header>
      <section>
        <p
          dangerouslySetInnerHTML={{
            __html: description || excerpt,
          }}
        />
      </section>
    </MyArticle>
  )
}

export default PostReview
